@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;700&display=swap);
* {
  box-sizing: border-box;
}

html {
  font-family: 'Noto Sans JP', sans-serif;
}

body {
  margin: 0;
  background-color: #EFEFEF;
}


.card {
  border-radius: 12px;
  box-shadow: 0 1px 8px rgba(0, 0, 0, 0.25);
}

.expenses-filter {
  color: white;
  padding: 0 1rem;
}

.expenses-filter__control {
  display: flex;
  width: 100%;
  align-items: center;
  justify-content: space-between;
  margin: 1rem 0;
}

.expenses-filter label {
  font-weight: bold;
  margin-bottom: 0.5rem;
  margin: 1rem;
}

.expenses-filter select {
  font: inherit;
  padding: 0.5rem 3rem;
  font-weight: bold;
  border-radius: 6px;
  
}
@media screen and (max-width:600px) {

  .expenses-filter{
    padding: 0 0rem;
  }
  .expenses-filter select {
    width: 100%;
    padding: 0.5rem 1rem;
  }
  
}
.expense-date {
  display: flex;
  flex-direction: column;
  width: 5rem;
  height: 3rem;
  border: 1px solid white;
  background-color: #383e42;
  color: white;
  border-radius: 12px;
  align-items: center;
  justify-content: center;
}

.expense-date__month {
  font-size: 0.75rem;
  font-weight: bold;
}

.expense-date__year {
  font-size: 0.75rem;
}

.expense-date__day {
  font-size: 1.5rem;
  font-weight: bold;
}

.expense-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.5rem;
  margin: 1rem 0;
  background-color: #d1d1d1;
}

.expense-item__description {
  display: flex;
  align-items: center;
  grid-gap: 1rem;
  gap: 1rem;
  flex-flow: column-reverse;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  flex: 1 1;
}

.expense-item h2 {
  color: #3a3a3a;
  font-size: 1.25rem;
  flex: 1 1;
  margin: 0 1rem;
  color: #3a3a3a;
}

.expense-item__price {
  font-size: 1.25rem;
  padding: 0.5rem 1.5rem;
  font-weight: bold;
  color: white;
  background-color: #383e42;
  border: 1px solid white;
  border-radius: 12px;
}

@media (max-width: 600px) {
  .expense-item__description {
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
    flex: 1 1;
  }

  .expense-item__description h2 {
    font-size: 1rem;
  }

  .expense-item__price {
    font-size: 1rem;
    padding: 0.5rem;
  }
}

.expenses-list {
  list-style: none;
  padding: 0;
}

.expenses-list__fallback {
  color: white;
  text-align: center;
}
.expenses {
  padding: 1rem;
  background-color:#7882A4 ;
  margin: 2rem auto;
}
.expenses button {
  font: inherit;
  cursor: pointer;
  padding: 1rem 2rem;
  border: 1px solid #383e42;
  background-color: #383e42;
  color: white;
  border-radius: 12px;
  margin-right: 1rem;

}


.new-expense__controls {
  display: flex;
  flex-wrap: wrap;
  grid-gap: 1rem;
  gap: 1rem;
  margin-bottom: 1rem;
  text-align: left;
}

.new-expense__control label {
  font-weight: bold;
  margin-bottom: 0.5rem;
  display: block;
  color:white;
}


.new-expense__control input {
  font: inherit;
  padding: 0.5rem;
  border-radius: 6px;
  border: 1px solid #ccc;
  width: 20rem;
  max-width: 100%;
}

.new-expense__control select {
  font: inherit;
  padding: 0.5rem;
  border-radius: 6px;
  border: 1px solid #ccc;
  width: 20rem;
  max-width: 100%;
}






.new-expense__actions {
  text-align: right;
}

.hja {
  font: inherit;
  cursor: pointer;
  padding: 1rem 2rem;
  border: 1px solid #383e42;
  background-color: #383e42;
  color: green;
  border-radius: 12px;
  margin-right: 1rem;

}

.new-expense {
  background-color: #7882A4;
  padding: 1rem;
  
  border-radius: 12px;
